*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* font-family:HelvetivaNeue; */
}
/* @font-face {
  font-family: HelveticaNeue;
  src: url(./assets/fonts/HelveticaNeue-Condensed.ttf);
